import helper from '../../common/helper.js';
export default {
	data() {
		return {
			time: '',
			PlanArray: ''
		};
	},
	onLoad(e) {
		this.xxscontent(e)
	},
	methods: {
		// 请求系统消息内容
		xxscontent(e) {
			uni.showLoading({
					title: "加载中....",
					mask: true
				}),
				uni.request({
					url: helper.websiteUrl + 'help/gethelp',
					method: 'GET',
					data: helper.postdata({
						"id": e.bzid,
						"Type":2
					}),
					success: res => {
						if (res.data.code == 200) {
							uni.hideLoading()
							this.PlanArray = res.data.data.baseinfo
						} else {
							helper.goout(res.data.info);
							uni.showToast({
								icon: 'none',
								title: res.data.info
							});
						}
					},

					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络异常,请稍后重试'
						});
					}
				});
		}
	}
}
